
#portfolio {
    font-family: 'Josefin Sans', sans-serif;
}
.bg-pic {
    background-image: url(https://scontent-sea1-1.xx.fbcdn.net/v/t1.0-9/1798609_924146140929829_2755872486769183154_n.jpg?oh=2fd0ddb09a177dd111901aa375eeb317&oe=5912E4CF);
    height: 98vh;
    background-size: cover;
    background-position: center;
    position: relative;
    background-repeat: no-repeat;
    background-attachment: fixed;
}
.big-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 100%;
    width: 100%;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.arrow {
    /*position: fixed;*/
    bottom: 0;
    color: rgb(237, 226, 215);
}
img {
    height: 100%;
}
.slider .indicators .indicator-item.active {
    background-color: rgb(122, 112, 102);
}



